<template>
  <!-- 内容页 -->
  <el-skeleton  v-if="loading" :rows="6" animated  />
  <div v-else>
    <!-- v-bind="topic" 将获取到的所有东西传递给下一级 -->
    <TopicItem v-for="topic in topics" :key="topic.id" v-bind="topic" />
    <el-pagination background layout="prev, pager, next" :page-size="40" :total="pageInfo[tab || 'all'].total" :current-page="page" @current-change="changePage">
</el-pagination>
  </div>
</template>

<script>
import { getTopics } from "../plugins/api";
import TopicItem from "../components/TopicItem.vue";
export default {
  data() {
    return {
      topics: [],
      loading: false,
      page: 1 ,
      // 页数数据
      pageInfo:{
        all:{
          total:240
        },
        good:{
          total: 720
        },
        share:{
          total: 160
        },
        ask:{
          total: 80
        },
        job:{
          total: 80
        },
        dev:{
          total: 210
        }
      }
    };
  },
  props: ["tab"],
  components: { TopicItem },
  watch:{
    tab:{
      async handler(val) {
        this.loading = true
        // 去拿分页器的数据
        const page = this.$route.query.page
        const res = await getTopics({ tab:val || 'all' , page: page || 1});
        // 把当前分页器变成第几页
        this.page = page * 1 || 1
        this.loading = false
        this.topics = res.data;
      },
      immediate: true,
    },
  },
  methods: {
    async changePage(page){
      this.page = page
      // 发送请求
      this.loading = true
      const res = await getTopics({ tab:this.tab || 'all',page});
      this.loading = false
      this.topics = res.data;
      // 使用 push 保持页面刷新的时候分页器显示的页面不会改变
      // 将页面数据传递给 watch  watch中去使用 page 
      this.$router.push(this.$route.path + `?page=${page}`)
    },
  }
};
</script>

