<template>
<!-- 内容的具体部分 -->
  <div class="topic-item">
    <router-link :to="`/user/${author.loginname}`">
      <img class="avatar_url" :src="author.avatar_url" alt=""> <!-- 头像 -->
    </router-link>
    <span class="count" v-if="!isUser">
      <span>{{reply_count}}</span> <!-- 评论 -->
      /
      <span class="countli">{{visit_count}}</span> <!--浏览量 -->
    </span>
    <template v-if="!isUser">
      <el-tag class="tag" v-if="top || good" size='small'  type='success' effect="dark">{{top? '置顶' : '精华'}}</el-tag> 
    <el-tag class="tag"  size='small' v-else type='info' >{{tab | tabTextTransform }}</el-tag> <!-- 类型 -->
    </template>      
    <router-link class="title" :to='`/topic/${id}`' ><h4>{{title}}</h4></router-link>  <!-- 标题 -->
    <span class="time">{{last_reply_at | fromNow}}</span> <!-- 时间 -->
  </div>
</template>

<script>
export default {
  props: [
    'isUser','author','good','last_reply_at','reply_count','tab','title','top','visit_count','id'
  ]
}
</script>

