  }
  .count{
    width: 80px;
    font-size: 12px;
    justify-content: center;
    display: flex;
    align-items: center;
    color: rgb(154, 156, 156);
    // font-style:oblique
    font-style: oblique;
    .countli{
      font-size: 13px;
      color: rgb(51, 49, 49);
