<template>
  <!-- 导航栏 -->
  <header>
    <div class="inner">
      <!-- 存放logo -->
        <router-link to="/">
             <img src="../assets/img/logo.png" alt="" class="logo" />
             </router-link>
      <!-- 导航栏右边 -->
       <div class="header-right">
        <!-- <router-link v-if="userInfo" to="/" class="home-link">未读消息</router-link>-->
        <!-- <router-link v-if="userInfo" to="/topic/create" class="home-link">编写文章</router-link> -->
         <el-menu 
         mode="horizontal"
         background-color="#2c3333"
         text-color="#fff"
         active-text-color="#ffd04b">
          <el-menu-item index="1"><router-link class="home-link" to="/"  >首页</router-link></el-menu-item>
          <el-submenu index="2" v-if="userInfo" >
            <template slot="title"  class="home-link">我的工作台</template>
            <el-menu-item index="2-1">
              <el-badge :hidden="messageCount == 0 " :value="messageCount">
              <router-link v-if="userInfo" to="/my/message" class="home-link">未读消息</router-link>
              </el-badge>
            </el-menu-item>
            <el-menu-item index="2-2">
              <router-link v-if="userInfo" to="/topic/create" class="home-link">编写文章</router-link>
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="3">
            <router-link to="/login" v-if="!userInfo">
            <el-button type="info" icon="el-icon-user-solid">登录</el-button>
            </router-link>
          </el-menu-item>
            <!-- 头像 -->
          <el-menu-item index="4">
            <router-link :to="`/user/${userInfo.loginname}`"  v-if="userInfo">
            <img class="tx" :src="userInfo.avatar_url" alt="" />
            </router-link>
          </el-menu-item>
          <el-menu-item index="5">
            <el-button type="info" v-if="userInfo" @click="logout" onclick="javascript:location.reload();">退出</el-button>
          </el-menu-item>
        </el-menu>
      </div>
    </div>
  </header>
</template>

<script>
import {getMessageCount} from '../plugins/api'
export default {
  data() {
    return {
      userInfo: null,
      messageCount: 0
    };
  },
  watch: {
    // 登录
    "$route.path": {
      async handler(val) {
        const userInfoJson = sessionStorage.getItem("userInfo");
        if (val === "/" && !this.userInfo && userInfoJson) {
          this.userInfo = JSON.parse(userInfoJson);
          const res = await getMessageCount(sessionStorage.getItem('accesstoken'))
          this.messageCount = res.data
        }
      },
      immediate: true
    },
  },
  created() {
    // 查看浏览器中 sessionStorage 是否存在值，存在则将他们进行转换
    // 登录
    const userInfoJson = sessionStorage.getItem("userInfo");
    if (userInfoJson) {
      this.userInfo = JSON.parse(userInfoJson);
    }
  },
  methods: {
    // 退出
    logout() {
      sessionStorage.removeItem("userInfo");
      sessionStorage.removeItem("accesstoken");
      this.userInfo = null;
      // 当在首页 ‘/’ 点击退出的时候，并不需要 push('/')
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
      
      this.$message({
        message: "退出成功",
        type: "success",
      });
    },
  },
};
</script>

<style lang='less'>
header {
  width: 100%;
  background-color: #2c3333;
  height: 55px;
  position: fixed;
  top: 0;
  .inner {
    width: 72.5%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: space-between;
    .logo {
      width: 140px;
      height: 60px;
      margin-top: 15px;
    }
    .el-button--info {
      background-color: rgb(90, 89, 83);
      border-color: rgb(90, 89, 83);
      padding: 7px 7px;
    }
    .home-link {
      font-size: 17px;
    }
    .tx {
      width: 33px;
      position: relative;
      top: 3px;
      right: 14px;
    }
  }
}
.el-menu--horizontal>.el-submenu .el-submenu__title {
  font-size: 17px;
}
</style>